import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './../../App.css'

export default class MacroNutrients extends Component {
  static propTypes = {
    macroNutrients: PropTypes.any.isRequired
  }

  static defaultPropTypes = {
    macroNutrients: {
      calories: 0,
      caloriesUnits: 'kcal',
      caloriesInUnits: 0,
      proteins: 0,
      proteinUnits: 'μg',
      proteinsInUnits: 0,
      carbohydrates: 0,
      carbohydratesUnits: 'μg',
      carbohydratesInUnits: 0,
      fat: 0,
      fatUnits: 'μg',
      fatInUnits: 0
    }
  }

  render() {
    return (
            <div className="macroNutrients">
                <div className="macroNutrient">
                    <div>Calories</div>
                    <div>{this.props.macroNutrients.caloriesInUnits} {this.props.macroNutrients.caloriesUnits}</div>
                </div>
                <div className="macroNutrient">
                    <div>Proteins</div>
                    <div>{this.props.macroNutrients.proteinsInUnits} {this.props.macroNutrients.proteinUnits}</div>
                </div>
                <div className="macroNutrient">
                    <div>Carbs</div>
                    <div>{this.props.macroNutrients.proteinsInUnits} {this.props.macroNutrients.proteinUnits}</div>
                </div>
                <div className="macroNutrient">
                    <div>Fats</div>
                    <div>{this.props.macroNutrients.fatInUnits} {this.props.macroNutrients.fatUnits}</div>
                </div>
            </div>
    )
  }
}
