import React, {Component} from 'react';
import './App.css';
import FoodSelector from './components/food-selector';
import MacroNutrients from './components/macro-nutrients';


import foods from './foods.json'
import nutrients from './nutrients.json'
import MicroNutrients from './components/micro-nutrients';
import SelectedFood from './components/selected-food';
import tutorial from './components/tutorial'
import logo from './logo3.png';
import {Icon} from 'antd';
import update from 'immutability-helper';

export default class App extends Component {

  state = {
    selectedFoods: [],
    macroNutrients: {
      calories: 0,
      caloriesUnits: 'kcal',
      caloriesInUnits: 0,
      proteins: 0,
      proteinUnits: 'μg',
      proteinsInUnits: 0,
      carbohydrates: 0,
      carbohydratesUnits: 'μg',
      carbohydratesInUnits: 0,
      fat: 0,
      fatUnits: 'μg',
      fatInUnits: 0
    },
    foodsForSelection: []
  }
  calculateMacroNutrients = (selectedFoods) => {
    const macroNutrients = Object.assign({}, {
      calories: 0,
      caloriesUnits: 'kcal',
      caloriesInUnits: 0,
      proteins: 0,
      proteinUnits: 'μg',
      proteinsInUnits: 0,
      carbohydrates: 0,
      carbohydratesUnits: 'μg',
      carbohydratesInUnits: 0,
      fat: 0,
      fatUnits: 'μg',
      fatInUnits: 0
    });
    selectedFoods.forEach(selectedFood => {
      selectedFood.amount = selectedFood.amount || 0;
      macroNutrients.calories += selectedFood.food.calories * selectedFood.amount;
      macroNutrients.proteins += selectedFood.food.proteins * selectedFood.amount;
      macroNutrients.carbohydrates += selectedFood.food.carbohydrates * selectedFood.amount;
      macroNutrients.fat += selectedFood.food.fat * selectedFood.amount;
    })
    macroNutrients.caloriesInUnits = macroNutrients.calories;
    macroNutrients.proteinsInUnits = macroNutrients.proteins;
    macroNutrients.carbohydratesInUnits = macroNutrients.carbohydrates;
    macroNutrients.fatInUnits = macroNutrients.fat;
    if (macroNutrients.proteinsInUnits >= 1000) {
      macroNutrients.proteinsInUnits /= 1000;
      macroNutrients.proteinUnits = 'mg'
    }
    if (macroNutrients.proteinsInUnits >= 1000) {
      macroNutrients.proteinsInUnits /= 1000;
      macroNutrients.proteinUnits = 'g'
    }
    if (macroNutrients.carbohydratesInUnits >= 1000) {
      macroNutrients.carbohydratesInUnits /= 1000;
      macroNutrients.carbohydratesUnits = 'mg'
    }
    if (macroNutrients.carbohydratesInUnits >= 1000) {
      macroNutrients.carbohydratesInUnits /= 1000;
      macroNutrients.carbohydratesUnits = 'g'
    }
    if (macroNutrients.fatInUnits >= 1000) {
      macroNutrients.fatInUnits /= 1000;
      macroNutrients.fatUnits = 'mg'
    }
    if (macroNutrients.fatInUnits >= 1000) {
      macroNutrients.fatInUnits /= 1000;
      macroNutrients.fatUnits = 'g'
    }

    // Rounding
    macroNutrients.caloriesInUnits = parseFloat(parseFloat(macroNutrients.caloriesInUnits).toFixed(2));
    macroNutrients.proteinsInUnits = parseFloat(parseFloat(macroNutrients.proteinsInUnits).toFixed(2));
    macroNutrients.carbohydratesInUnits = parseFloat(parseFloat(macroNutrients.carbohydratesInUnits).toFixed(2));
    macroNutrients.fatInUnits = parseFloat(parseFloat( macroNutrients.fatInUnits).toFixed(2));

    return macroNutrients;
  }

  calculateMicroNutrients = (selectedFoods) => {
    const microNutrients = {};
    nutrients.forEach(nutrient => {
      microNutrients[nutrient.name] = {
        ...nutrient,
        amount: 0,
        percentage: 0,
        status: undefined
      };
      selectedFoods.forEach(selectedFood => {
        const foodNutrients = selectedFood.food.nutrients[nutrient.name];
        if (typeof foodNutrients === 'undefined') {
          console.log(`Food ${selectedFood.name} is missing nutrient: ${nutrient.name}`);
        }
        microNutrients[nutrient.name].amount += (foodNutrients || 0) * selectedFood.amount;
      })
      microNutrients[nutrient.name].percentage = parseInt((microNutrients[nutrient.name].amount * 100) / microNutrients[nutrient.name].rda, 10)
      microNutrients[nutrient.name].amount = parseInt(microNutrients[nutrient.name].amount, 10)
      microNutrients[nutrient.name].amountInUnits = microNutrients[nutrient.name].amount
      microNutrients[nutrient.name].amountUnit = 'μg'
      if (microNutrients[nutrient.name].amountInUnits >= 1000) {
        microNutrients[nutrient.name].amountInUnits /= 1000;
        microNutrients[nutrient.name].amountUnit = 'mg'
      }
      if (microNutrients[nutrient.name].amountInUnits >= 1000) {
        microNutrients[nutrient.name].amountInUnits /= 1000;
        microNutrients[nutrient.name].amountUnit = 'g'
      }
      microNutrients[nutrient.name].amountInUnits = parseFloat(parseFloat(microNutrients[nutrient.name].amountInUnits).toFixed(2));
    })
    
    console.log(microNutrients);
    return microNutrients;
  }

  updateMacroNutrients = () => {
    this.setState({
      macroNutrients: this.calculateMacroNutrients(this.state.selectedFoods)
    });
  }
  updateMicroNutrients = () => {
    this.setState({
      microNutrients: this.calculateMicroNutrients(this.state.selectedFoods)
    });
  }
  updateFoodSelector = () => {
    const allPossibleFoods = foods.map(food => food.name);
    const allSelectedFoods =this.state.selectedFoods.map(sf => sf.food.name);
    this.setState({
      foodsForSelection: allPossibleFoods.filter(f => !allSelectedFoods.includes(f))
    })
  }
  onFoodSelect = selectedFoodName => {
    this.setState({
      selectedFoods: [
        ...this.state.selectedFoods,
        {
          food: foods.find(f => f.name === selectedFoodName),
          amount: 100
        }
      ]
    }, () => {
      this.updateMacroNutrients();
      this.updateMicroNutrients();
      this.updateFoodSelector();
    })
  }
  
  onFoodRemove = (removedFood) => {
    const removedFoodIndex = this.state.selectedFoods.findIndex(f => f.food.name === removedFood.food.name);
    const updatedSelectedFoods = update(this.state.selectedFoods, {$splice: [[removedFoodIndex, 1]]});
    this.setState({selectedFoods: updatedSelectedFoods}, () => {
      this.updateMacroNutrients();
      this.updateMicroNutrients();
      this.updateFoodSelector();
    });
  }
  onFoodAmountChange = (updatedSelectedFood) => {
    const updatedFoodIndex = this.state.selectedFoods.findIndex(f => f.food.name === updatedSelectedFood.food.name);
    const updatedSelectedFoods = update(this.state.selectedFoods, {$splice: [[updatedFoodIndex, 1, updatedSelectedFood]]});
    this.setState({selectedFoods: updatedSelectedFoods}, () => {
      this.updateMacroNutrients();
      this.updateMicroNutrients();
    });
  }
  
  componentDidMount() {
    this.setState({
      foodsForSelection: foods.map(food => food.name)
    })
    tutorial();
  }

  render() {
    return (
      <div className="App">
        <div className="leftPanel">
          <div className="logo">
            <img alt="Logo" src={logo} />
          </div>
            <FoodSelector className="foodSelector" foods={this.state.foodsForSelection} onChange={this.onFoodSelect} />
            {this.state.selectedFoods.length === 0 && 
            <div className="addFoods">
              <Icon className="animate-flicker" style={{fontSize: '50pt', color: 'gray'}} type="arrow-up" />
            </div>}
            {this.state.selectedFoods.length > 0 && <h3 className="selectedFoodsLabel">Selected foods</h3>}
            
            {
              this.state.selectedFoods.map(selectedFood => 
                <SelectedFood
                  key={selectedFood.food.name}
                  selectedFood={selectedFood}
                  onChange={this.onFoodAmountChange}
                  onRemove={this.onFoodRemove}
                ></SelectedFood>
              )
            }
        </div>
        <div className="rightPanel">
        
          <MacroNutrients macroNutrients={this.state.macroNutrients} />
          <MicroNutrients definitions={nutrients} microNutrients={this.state.microNutrients}></MicroNutrients>
        </div>
      </div>
    );
  }
}