import React, { Component } from 'react'
import PropTypes from 'prop-types'
import {Input, Button, Icon} from 'antd'; 

export default class SelectedFood extends Component {
  static propTypes = {
    onChange: PropTypes.func.isRequired,
    onRemove: PropTypes.func.isRequired,
    selectedFood: PropTypes.shape({
        food: PropTypes.any,
        amount: PropTypes.number
    }).isRequired
  }

  render() {
    return (
      <div className="selectedFood">
        <h2>{this.props.selectedFood.food.name}</h2>
        <div className="selectedFoodAmount">
            <Input addonAfter="grams" min="0" max="5000" type="number" onChange={(e) => {
                this.props.onChange(Object.assign(this.props.selectedFood, { amount: parseInt(e.currentTarget.value, 10) }));
            }} defaultValue={this.props.selectedFood.amount} />
            <Button onClick={() => this.props.onRemove(this.props.selectedFood)} className="deleteBtn"><Icon type="delete" /></Button>
        </div>
      </div>
    )
  }
}
